import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { TrendsComponent } from '../trendings/trends/trends.component';
import { ProfileComponent } from '../profile/profile.component';
import { PagenotfoundComponent } from '../pagenotfound/pagenotfound.component'
import { AuthGuard} from '../../_guards';
import { CreateComponent } from '../blog/create/create.component';
import { DisplayEblogsComponent } from '../display-eblogs/display-eblogs.component';
import { StoriesComponent } from '../stories/stories.component';
import { LoginComponent } from '../admin/login/login.component';
import { RegisterComponent } from '../admin/register/register.component';
import { UpdateComponent } from '../blog/update/update.component';
import { DraftPublishComponent } from '../blog/draft-publish/draft-publish.component';
import { TextEditorComponent } from '../editor/text-editor/text-editor.component';
import { MediumEditorComponent } from '../editor/medium-editor/medium-editor.component';
const routes: Routes = [
  {path : 'login', component: LoginComponent},
  {path : 'register', component: RegisterComponent},
  {
    path: '',
    component: HomeComponent,canActivate: [AuthGuard] 
  },
  {
    path: 'timeline',
    component: HomeComponent,canActivate: [AuthGuard] 
  },
  {
    path: 'trend',
    component: TrendsComponent, canActivate: [AuthGuard] 
  },
  {
    path: 'create',
    component: CreateComponent, canActivate: [AuthGuard] 
  },
  {
    path: 'create/story',
    component: MediumEditorComponent, canActivate: [AuthGuard] 
  },
  {path : '@blogger/:username', component: ProfileComponent},

  {path : 'update/:id', component: UpdateComponent, canActivate: [AuthGuard]},

  {path : 'drafts/:id/:username', component: DraftPublishComponent, canActivate:  [AuthGuard]  },

  {path : 'blog/:id/:username/:title', component: DisplayEblogsComponent },

  {path : 'stories/:id/:username/:title', component:StoriesComponent},
  {
    path: 'write',
    component: TextEditorComponent,  canActivate:  [AuthGuard] 
  },
  {
    path: '**',
    component: PagenotfoundComponent 
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomeRoutingModule { }