import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewEncapsulation,
  ViewChild,
  QueryList
} from '@angular/core';

import { LoaderService, FooterServiceService, ImagesService } from '../../../_services';
import { v4 as uuid } from 'uuid';
import { UUID } from 'angular2-uuid';
import { Posts } from '../../../models';
import { INTERNAL_BROWSER_DYNAMIC_PLATFORM_PROVIDERS } from '@angular/platform-browser-dynamic/src/platform_providers';
import { PostMService } from '../../../_services/postm.service';



@Component({
  selector: 'text-editor',
  templateUrl: './text-editor.component.html' ,
  styleUrls: ['./text-editor.component.css',
 
  '../../../../../node_modules/quill/dist/quill.bubble.css'
],

})
export class TextEditorComponent implements AfterViewInit,OnChanges {
 @ViewChild('ce') ces:QueryList<ElementRef>;
  arrow:boolean;
  title:string;
  bold:boolean;
  editor:any;
  fileToUpload: File = null
  key: any;
  postImage:any;
  calledOnce:boolean = false;
  isPushed:boolean;
  postM: Posts = new Posts
  constructor( 
               private elementRef:  ElementRef,
               private loaderService: LoaderService,
               private footerService: FooterServiceService,
               private imagesService : ImagesService,
               private postMob : PostMService,
              ){

    }
    private showLoader(): void {
      this.loaderService.show();
      }
      private hideLoader(): void {
      this.loaderService.hide();
      } 
  
      private showFooter():void{
        this.footerService.show();
      }
      private hideFooter():void{
        this.footerService.hide();
      }
  
  

   
  
  ngAfterViewInit(){
 


    this.editor = document.querySelector('.editor');
    this.editor.contentEditable = true ;
    // this.editor.addEventListener('keyup',function(event:any){
    // this.postM.content =  event.target.innerHTML ;
    // });
    this.editor.style.fontSize = '20px'; 
    this.editor.style.padding = '10px';
    this.editor.style.minHeight = '300px';

    
  }
 
  command(name){

    // this.editor.execCommand(name, false, null)
   
    document.execCommand(name, false, null);
    
  }
  
  commandYoutube(name){
  
    var URL = prompt('Please enter only video ID');


    if(URL !== null || undefined){

    document.execCommand('insertHTML',false, '<div class="embed-responsive embed-responsive-16by9"><iframe class="embed-responsive-item" src="https://www.youtube.com/embed/'+ URL +'" ></iframe></div>');
    }
  }
  commandLink(name){

    var URL = prompt('Please enter link')

    if(URL !== null || undefined){
    document.execCommand('CreateLink',false,URL);
    }
  }
 
  commandImage(name){
    // this.showLoader();
    var input = $(document.createElement("input"));
    input.attr('type',"file");
    for(var i=0; i < input.length ; i++){
    input[i].addEventListener("change",(event:any)=>{
     var files: FileList = event.target.files;
      this.fileToUpload = files.item(0);
    if (this.fileToUpload.type === 'image/jpeg' || 'image/png') {
        this.showLoader();
        if(this.calledOnce === false){
          this.key = uuid();
      this.imagesService.getHash(this.fileToUpload,this.key).subscribe(res =>{
      this.hideLoader();
      this.postM._id = this.key;
      this.calledOnce = true;
      document.execCommand('insertHTML',false,'<img style="width:100%;border-radius:5px;" src="https://res.cloudinary.com/ebloginc/image/upload/v1524888649/eblogpostimages/' + this.key +'" />')
      });
    }else{
      this.showLoader();
      this.key = uuid();
      this.imagesService.getHash(this.fileToUpload,this.key).subscribe(res =>{
        this.hideLoader();
        this.calledOnce = true;
        document.execCommand('insertHTML',false,'<img style="width:100%;border-radius:5px;" src="https://res.cloudinary.com/ebloginc/image/upload/v1524888649/eblogpostimages/' + this.key +'" />')

        });


    }
    } 

    });
    }
    input.trigger('click');

    



  
  }
  commandHr(name){
   
    document.execCommand('insertHTML', false, '<hr style="border-bottom:1px dashed grey;">');
    
  }
  commandBq(name){
   
    document.execCommand('insertHTML', false, '<br><div  style=" text-align:center;   border-left: 6px solid #333; font-family: Georgia, serif;font-size: 20px;font-style: italic;margin: 0.25em 0;padding: 0.25em 40px;line-height: 1.45;position: relative;color: #383838;">quote</div><br>');
    
  }
  commandHeading(name){

   
   
    document.execCommand('formatBlock', false, '<h2>');
   
    
  } 

  commandColor(name){

    document.execCommand("HiliteColor", false, "#f4c20d")
  }
   
  ngOnChanges(changes: SimpleChanges) {
    


  
  }

 
  navHide(){

    this.hideFooter();  
    this.arrow = true;
    
    
    }
    
    navBack(){
      this.showFooter();
      this.arrow = false;
    }
    postMobile(){
      this.showLoader();
     this.isPushed = true;
     var str  = this.title;
     this.postM.title = str.replace(/\s+/g, '-');
     this.arrow = false
     if(this.postM._id === undefined || null){
      this.postM._id = uuid() ;

     }
      this.postMob.postM(this.postM).subscribe(res =>{
          this.hideLoader();
          window.location.reload();
      });

    
    
    }
  
    
}