import {catchError, map} from 'rxjs/operators';
import { Injectable,Output } from '@angular/core';
import {Http, Headers} from "@angular/http";
import { Post, ReadList } from '../models';
import { Get } from '../models';
import { User } from '../models';
import { AuthService } from '../_services/auth.service';


import { EventEmitter } from 'events';
import {throwError as observableThrowError,  Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})




export class ReadlistService {

  @Output () change : EventEmitter = new EventEmitter();
  url :string = '/';

  constructor( private http : Http, private authService: AuthService) { }

  headers = new Headers ({
    'Content-Type': 'application/json',
    Accept: 'application/json',
    //  'Authorization': this.authService.authToken,
  });

  updateHeaders() {

    this.headers.set('Authorization', this.authService.authToken);
  }

 addReadlist(RL:ReadList):Observable<any>{

  this.updateHeaders();
  var user = this.authService.getCurrentUser()  ;
  var id = user.username;
  let URL = this.url + 'readlist/add/' + id ;
  return this.http.post(URL,RL,{headers:this.headers})
  .map(res => res.json())
  .catch(err =>{
    return observableThrowError(err);
  });

 }

getReadlist(uid:string):Observable<any>{

  this.updateHeaders();

  let URL = this.url + 'readlist/get/' + uid ;
  return this.http.post(URL,{headers:this.headers})
  .map(res => res.json())
  .catch(err =>{
    return observableThrowError(err);
  });

 }
 removeReadlist(id:string):Observable<any>{

  this.updateHeaders();

  let URL = this.url + 'readlist/remove/' + id ;
  return this.http.post(URL,{headers:this.headers})
  .map(res => res.json())
  .catch(err =>{
    return observableThrowError(err);
  });

 }

}
