import { Component, OnInit, DoCheck, ViewChild, ElementRef } from '@angular/core';
import { AuthService, LoaderService, ImagesService, ReadlistService } from '../../../_services';
import { PostService } from '../../../_services';
import { Post, ReadList } from '../../../models';
import { Get } from '../../../models';
import { User } from '../../../models';
import {MatSnackBar} from '@angular/material';


import { Http, Response } from '@angular/http';
import { Router } from '@angular/router';
import { PostMService } from '../../../_services/postm.service';

@Component({
  selector: 'app-trends',
  templateUrl: './trends.component.html',
  styleUrls: ['./trends.component.css']
})
export class TrendsComponent implements OnInit {
  @ViewChild('error') error: ElementRef;

  blog = [];
  get : Get = new Get();
  posts:any = [];
  user : Object;
  post : Post = new Post();
  RL : ReadList = new ReadList();
  defaultProfile:string ='../../../assets/images/mac.jpg' ; 
  constructor( private authService : AuthService,
               private postService : PostService,
               private router     : Router,
               private loaderService : LoaderService,
               private postMService : PostMService,
               private imagesService: ImagesService,
               private ridService : ReadlistService,
               public snackBar: MatSnackBar,
              ) {
               this.user = authService.getCurrentUser();
                }
private showLoader(): void {
this.loaderService.show();
}
private hideLoader(): void {
this.loaderService.hide();
} 

openSnackBar(message: string, action: string) {
  this.snackBar.open(message, action, {
    duration: 2000,
  });
}
  ngOnInit() {
    window.scrollTo(0,0);

     this.showLoader();
    this.authService.getProfile().subscribe(profile =>{
          // this.user = profile.user;
          // console.log(this.user);
    });
    this.postService.getEblogs().subscribe((res) =>{
            // this. post = data;   
            this. get = res.blog; 
            this.posts = res.blog;
    
     this.postMService.getPostsM().subscribe(res =>{
       
         this.post = res.blog;
         const length:any = this.post;
         for (var i=0; i < length.length ;i++ ){
            
         
    var date_asc = function(a, b) {
      if(a.createdtime > b.createdtime) return 1;
      if(a.createdtime < b.createdtime) return -1;
      return 0;
    };
    var date_desc = function(a, b){
      if(a.createdtime < b.createdtime) return 1;
      if(a.createdtime > b.createdtime) return -1;
      return 0;
    }

   


  }
      
          var date :any =   this.post
          var date1:any =  date.concat(this.posts);
          var sort  : Get [] ;
         
          sort = date1.slice(0) 
         
          sort.sort(date_asc);
          this. blog  =  sort;
         this.hideLoader();
         
    });
  });
}

deletestoryApi(id:string,blogUser:string){

  this.showLoader();
  this.postMService.deletePostM(id,blogUser).subscribe(res =>{
    this.imagesService.deleteHash(id).subscribe(res =>{
      this.postService.removeComment(id).subscribe(res =>{
        this.postService.removeAd(id).subscribe(res =>{
          this.hideLoader();
          window.location.reload();
        });
      })
    });
   
  });
}

deleteblogApi(id:string,blogUser){
   
  this.showLoader();
  this.postService.deleteEblogs(id,blogUser).subscribe(res =>{
    this.imagesService.deleteHash(id).subscribe(res =>{
      this.postService.removeComment(id).subscribe(res =>{
        this.postService.removeAd(id).subscribe(res =>{
          this.hideLoader();
          window.location.reload();
        });
      })
    });
   
  });
}
readliststoryApi(id:string){
  this.showLoader();  
  this.RL.rid = id;
    this.ridService.addReadlist(this.RL).subscribe(res=>{
      this.triggerFalseClick();
      this.hideLoader();
    });

}
readlistblogApi(id:string){
  this.showLoader();  
    this.RL.rid = id;
      this.ridService.addReadlist(this.RL).subscribe(res=>{
        this.hideLoader();
        this.triggerFalseClick();
      });
}
triggerFalseClick() {
  let el: HTMLElement = this.error.nativeElement as HTMLElement;
  el.click();
}

navigateBlog(id:String, username:string, title:String){
    this.router.navigate(['/blog',id,username,title]);
}
navigateStory(id:String,username:string,title:String){
  this.router.navigate(['/stories',id,username,title]);

}

}
