
import {throwError as observableThrowError,  Observable } from 'rxjs';

import {catchError, map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { AuthService, LoaderService } from '.';
import { Posts, Get, User } from '../models';

 
@Injectable()
export class PostMService {

    serverURL = '';
  constructor(private authService : AuthService, private http: Http,
    private loaderService: LoaderService
  ) {  }

   
  headers = new Headers ({
    'Content-Type': 'application/json',
    Accept: 'application/json',
    //  'Authorization': this.authService.authToken,
  });

  updateHeaders() {

    this.headers.set('Authorization', this.authService.authToken);
  }


  postM(postM:Posts):Observable<any>{
    this.updateHeaders();
    let user = this.authService.getCurrentUser();
    let blogUser = user.username;
    let URL = this.serverURL + '/postM/create/' + blogUser ;
    return this.http.post(URL,postM,{headers: this.headers}).pipe(
    map(res => res.json()),
    catchError(err =>{
      return observableThrowError (err);
    }),)
    
  }

    getPostM(blogUser:User):Observable<any>{
    let URL = this.serverURL + '/postM/getbybloguser/' ;
    return this.http.post(URL,blogUser,{headers:this.headers}).pipe(
    map(res => res.json() ),
    catchError(err => {
      return observableThrowError (err);
    }),)
  }

  getPostMById(id:Posts):Observable<any>{
    let URL = this.serverURL + '/postM/get/content/id' ;
    return this.http.post(URL,id,{headers:this.headers}).pipe(
    map(res => res.json() as Posts  ),
    catchError(err => {
      return observableThrowError (err);
    }),)
  }
  getPostMByExceptId(id:string):Observable<any>{
    let URL = this.serverURL + '/postM/getexceptusername/'+ id ;
    return this.http.get(URL,{headers:this.headers}).pipe(
    map(res => res.json() as Posts ),
    catchError(err => {
      return observableThrowError (err);
    }),)
  }
   
  getPostsM():Observable<any>{
    let URL = this.serverURL + '/postM/getposts' ;
    return this.http.get(URL,{headers:this.headers}).pipe(
    map(res => res.json()),
    catchError(err => {
      return observableThrowError (err);
    }))
  }

  checkM():Observable<any>{
    let URL = this.serverURL + '/postM/check' ;
    return this.http.get(URL,{headers:this.headers}).pipe(
    map(res => res.json()),
    catchError(err => {
      return observableThrowError (err);
    }))
  }
  deletePostM(id:string,username:string):Observable<any>{
    this.updateHeaders();
    let URL = this.serverURL + '/postM/delete/' + id  +'/'+ username ;
    return this.http.delete(URL,{headers:this.headers}).pipe(
    map(res => res.json()),
    catchError(err =>{
      return observableThrowError (err);
    }),) 
  }
}
