saveDraft(){
    this.showLoader();
  this.isSaving = true;

  var str  = this.post.title;
  this.post.title = str.replace(/\s+/g, '-');
  this.postService.drafts(this.post).subscribe(res =>{
    this.isSaving = false;
    this.hideLoader();
    this.msg = "saved!";
  });

  window.location.reload();
    

}

