add(event: MatChipInputEvent): void {
  let input = event.input;
  let value = event.value;

  // Add our fruit
  if ((value || '').trim()) {
    this.tag.push({ name: value.trim() });
  }

  // Reset the input value
  if (input) {
    input.value = '';
  }
}

remove(tag: any): void {
  let index = this.tag.indexOf(tag);

  if (index >= 0) {
    this.tag.splice(index, 1);
  }
}

