import { Directive, ElementRef, Input, Renderer, Output, EventEmitter  } from '@angular/core';
import * as $ from 'jquery';
@Directive({ selector: '[toolbar]' })

export class ToolbarDirective {

    constructor(el: ElementRef, renderer: Renderer) {
        try{
                    
          $(function(){
            var stickyHeaderTop = $('[toolbar]').offset().top;
          
            $(window).scroll(function(){
                    if( $(window).scrollTop() > stickyHeaderTop ) {
                            $('[toolbar]').css({position: 'fixed',bottom:'0', 'box-shadow': 'none','background-color':'transparent'});
                          
                    } else {
                            $('[toolbar]').css({position: 'static',background:"#ddd",'box-shadow': '1px 1px 5px 0.5px  gray' });
                          
                    }
            });
        });



    }
catch(err){
  console.log(err);
}
}
}
