deletestoryApi(id:string,username:string){
this.showLoader();

  this.postMob.deletePostM(id,username).subscribe(res =>{
    this.imageService.deleteHash(id).subscribe(res =>{
      this.postService.removeComment(id).subscribe(res =>{
        this.postService.removeAd(id).subscribe(res =>{
          this.hideLoader();
          window.location.reload();
        });
      });
    });
   
  })
}
deleteblogApi(id:string,username:string){
  this.showLoader();
   this.postService.deleteEblogs(id,username).subscribe(res =>{
    this.imageService.deleteHash(id).subscribe(res =>{
      this.postService.removeComment(id).subscribe(res =>{
        this.postService.removeAd(id).subscribe(res =>{
          this.hideLoader();
          window.location.reload();
        });
      })
    });
   
  })
}
