import {map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { HttpModule } from '@angular/http';
import {isNullOrUndefined} from 'util';
import {Observable, Subject} from 'rxjs';
import { tokenNotExpired } from 'angular2-jwt';
import { User } from '../models';
import { Users } from '../models';
import { LoaderService } from '.';


 @Injectable()
export class AuthService {
  authToken: any;
  user: any;
  url: String = '';
  constructor(private http: Http,
              
  ) {
      // this.isDev = true;  // Change to false before deployment
      }

  registerUser(user) {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post( this.url + '/users/register', user, {headers: headers}).pipe(
      map(res => res.json()));
  }

  validUsername(username:User){

    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post( this.url + '/users/validusername/' ,username , {headers: headers}).pipe(
      map(res => res.json()));
  }
  validEmail(email:User){

    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post( this.url + '/users/validemail/' ,email , {headers: headers}).pipe(
      map(res => res.json()));
  }


  login(user) {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.url + '/users/login', user, {headers: headers}).pipe(
    map(
      res => res.json(),
      err => console.log('Error:', err)
    ));
  }

  getProfile() {
    const headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(this.url + '/users/profile', {headers: headers}).pipe(
      map(res => res.json()));
  }

  getCurrentUser(): User {

    const userString = localStorage.getItem('user');
    if (!isNullOrUndefined(userString)) {
      const user: User = JSON.parse(userString);
           return user;
    } else {
      return null;
    }
  }

  getUserById(User): Observable<any> {
    const headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(this.url + '/users/userid', {headers: headers}).pipe(
      map(res => res.json()));

  }

  getUsers(userName: User): Observable<any> {
    const headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.post(this.url + '/users/getusers/' ,userName , {headers: headers}).pipe(
    map(res => res.json()));
  }

  getUsersByUsername(username: string): Observable<any> {

    const  headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(this.url + '/users/getuserbyusername/' + username , {headers: headers}).pipe(
    map(res => res.json()));
  }

  storeUserData(token, user) {
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn() {
    return tokenNotExpired('id_token');
  }

  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }
}