import { Component, OnInit, OnDestroy, Inject, HostListener } from '@angular/core';
import { AuthService } from '../../../_services';
import { User } from '../../../models';
import { Router } from '@angular/router';
import { NavbarDirective } from '../../../animations/navbarScroll.directive'
import { Subscription } from 'rxjs';
import { LoaderState } from '../../../models';
import { FooterServiceService } from '../../../_services'
import * as moment from 'moment';
import { DOCUMENT } from "@angular/platform-browser";


@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit ,OnDestroy{
 
  user :User = new User();
  show = false;
   private subscription: Subscription;
  constructor(
             @Inject(DOCUMENT) private doc: Document,
             private authService : AuthService,
               private router : Router   ,
               private footerService : FooterServiceService    ) { 
   
                this.user = this.authService.getCurrentUser();
  }
 

  ngOnInit() {

  
    this.authService.getProfile().subscribe(res =>{

      //  this.user = res.user;
    })
    this.subscription  = this.footerService.loaderState
    .subscribe((state: LoaderState) => {
      this.show = state.show;
  });
  }
ngOnDestroy(){
  this.subscription.unsubscribe();

}
  onLogoutClick() {

    
    this.authService.logout();
    localStorage.clear();
    this.router.navigate(['/login']);
  }


  onProfileClick() {

    this.router.navigate(['/@blogger/' + this.user.username ]);
  }


 onChat() {

  this.router.navigate(['/chat']);
 }

 loggin(): boolean {
 if (this.authService.loggedIn() ) {
  return true;
} else {
  return false;
}

 }

}
