import { Component, OnInit, ViewChild, ElementRef, DoCheck} from '@angular/core';
import { AuthService, PostService, LoaderService } from '../../../_services';
import { Posts, Post } from '../../../models';
import { Router } from '@angular/router';
import {MatSnackBar} from '@angular/material';

@Component({
  selector: 'app-drafts',
  templateUrl: './drafts.component.html',
  styleUrls: ['./drafts.component.css']
})
export class DraftsComponent implements OnInit {
  @ViewChild('error') error: ElementRef;

  user: any;
  posts: Posts = new Posts();
  draft:Posts = new Posts();
  errorMessage :string;
  constructor(public snackBar: MatSnackBar, private authService: AuthService, private postService: PostService , private router : Router, private loaderService: LoaderService) { 

    this.user = this.authService.getCurrentUser();
  }
  private showLoader(): void {
    this.loaderService.show();
    }
    private hideLoader(): void {
    this.loaderService.hide();
    } 

    openSnackBar(message: string, action: string) {
      this.snackBar.open(message, action, {
        duration:1000,
      });
    }
  ngOnInit() {
    window.scrollTo(0,0);
    this.showLoader();
    this.postService.getdrafts().subscribe(res =>{
      this.posts = res.blog;
      this.hideLoader();
      if(res.blog[0] != undefined){
      this.draft = res;
      // console.log(this.post.blog);
     
            
      }
      if(res.blog[0] == undefined){
       
      this.errorMessage = "oops!";
      }
    });
  }


 clickMethod(title: string, id:string ) {
   
  if(confirm("Are you sure want to delete " + title )) {
    this.showLoader();
   this.postService.deletedraft(id).subscribe(res =>{
     this.hideLoader();
   window.location.reload();
   });
         
   
  
  }else{
     
   
  }
}

}
