import { Pipe, PipeTransform } from '@angular/core';


@Pipe ({
    name : 'content'
})

export class ContentPipe implements PipeTransform{
  
  
  transform(value: string): string {
    if(value !==  undefined || null) {
    return value.replace(/(<([^>]+)>)/ig,"").replace(/<img[^>"']*((("[^"]*")|('[^']*'))[^"'>]*)*>/g,"");
 
  }

}
}