export class RegisterComponent implements OnInit {

  username: string;
  email: string;
  password1: string;
  password2: string;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  isEditable: boolean = true;
  fullImagePath :any; 
  url: URL = new URL();
  fileToUpload: File = null;
  public user : any;
  unameValid : string = null;
  unameNotValid : string = null;
  emailNotValid: string = null;
  emailValid: string = null;
  isPushed: boolean;
  User: User = new User();
  usernameTyped:string = 'Atleast 5 letters';
  emailTyped:string = 'you@example.com';
  passwordTyped:string = '';

  constructor(public dialog: MatDialog, public snackBar: MatSnackBar,  private authService : AuthService, private imageService : ImagesService , private router:Router, private loaderService:LoaderService,private _formBuilder: FormBuilder) {
    // this.fullImagePath = 'assets/images/therealdealportfoliohero.jpg'
    }
  private showLoader(): void {
  this.loaderService.show();
  }
  private hideLoader(): void {
  this.loaderService.hide();
} 

  ngOnInit() {

    this. user = {
      username: this.username,
      password1: this.password1,
      email: this.email,
      password2: this.password2
    }
  
  }

  openDialog(): void {
    let dialogRef = this.dialog.open(DialogContentRegisterDialog, {
    // width:'100%',
      
    });

    dialogRef.afterClosed().subscribe(result => {

      if(result !== undefined || null){
         
      this.showLoader();
      this.isPushed = true;
    const user = {
      username: this.user.username,
      email: this.user.email,
      password: this.user.password2
    }
     
   this.authService.registerUser(user).subscribe(res => {
      
      const key =  res.data.username  ;
       this.url.URL = result.image;
    this.imageService.uploadProfilePicURL(result, key).subscribe(res =>{
 

      this.hideLoader();
      this.isPushed = false;
      this.router.navigate(['/timeline']);
    });
                            
           
   });
  // console.log(result);
  }else{
    this.dialog.closeAll();
  }
    });
  

}
  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }

  onRegisterSubmit(){
    this.showLoader();
      this.isPushed = true;
    const user = {
      username: this.user.username,
      email: this.user.email,
      password: this.user.password2
    }
      //  console.log(user);
   this.authService.registerUser(user).subscribe(res => {
             
            this.hideLoader();
                 this.router.navigate(['/profilepic/' + this.user.username]);
           
   });
   
      
    // console.log(this.user);

  }
  validUsername(value){
          this.showLoader();
          this.User.username = value;
    this.authService.validUsername(this.User).subscribe(res => {
        
      // console.log(res);
      if(res.success === true){
         this.unameValid = 'oops';   
         this.unameNotValid = '';
         this.usernameTyped = 'username not taken';
         this.hideLoader();
      }else{
         this.unameNotValid = 'oops';
         this.unameValid = ''; 
         this.usernameTyped = 'username is already taken'; 
         this.hideLoader();
      }
   
    });
  }
  validEmail(value){
          this.showLoader();
          this.User.email=value
    this.authService.validEmail(this.User).subscribe(res => {
        
      // console.log(res);
      if(res.success === true){
         this.emailValid = 'oops';   
         this.emailNotValid = '';
         this.emailTyped = 'email not registerd';
         this.hideLoader();
      }else{
         this.emailNotValid = 'oops';
         this.emailValid = '';  
         this.emailTyped = 'email is already registerd';
         this.hideLoader();
      }
   
    });
  }
}

