
import {throwError as observableThrowError,  Observable } from 'rxjs';

import {catchError, map} from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Http ,Headers, RequestOptions,  ResponseContentType} from '@angular/http';


import { AuthService } from '../_services/auth.service';
import { PostService } from '../_services/post.service';
// import { Image } from '../images/image';
import { FileUploader } from 'ng2-file-upload';
import { Users } from '../models';
import { DomSanitizer, SafeResourceUrl, SafeUrl } from '@angular/platform-browser';


@Injectable()
export class ImagesService {


 user : Users = new Users();
  filesToUpload: Array<File> = [];
  serverUrl : string = '';
  constructor( private http : Http,
               private authService : AuthService,
               private postService : PostService,
               private _sanitizer : DomSanitizer
              ) {
             
                
                }

      headers = new Headers ({
      'Content-Type' : 'image/jpeg',
      
    });
   
  
    updateprofilePic(fileToUpload: File, username:string){

      const url =  this.serverUrl + '/profilepic/update/' + username;
      const formData: FormData = new FormData();
      formData.append('file', fileToUpload, fileToUpload.name);
      return this.http.post(url, formData).pipe(
        map(() => { return true; }),
        catchError(err =>{
          return observableThrowError (err)
        }),);


    }
    
  
   uploadProfilePic(fileToUpload: File, username:string): Observable<any> {
    const url =  this.serverUrl + '/profilepic/upload/' + username;
    const formData: FormData = new FormData();
    formData.append('file', fileToUpload, fileToUpload.name);
    return this.http.post(url, formData).pipe(
      map(() => { return true; }),
      catchError(err =>{
        return observableThrowError (err)
      }),);
   }
   uploadProfilePicURL(URL:any, key: string): Observable<any> {
    const url = this.serverUrl + '/profilepic/uploadbyurl/'+ key ;
     var headers = new Headers ({
      'Content-Type' : 'application/json' });  
    return this.http.post(url, URL ,{headers: headers}).pipe(
      map(() => { return true; }),
      catchError(err =>{
        return observableThrowError (err)
      }),);
   }
   
   getProfile(): Observable<any>{
    var user = this.authService.getCurrentUser()  ;
    var name = user.username;
    const url = this.serverUrl + '/profilepic/getprofile/'+ name;
    return this.http.get(url,{headers : this.headers , responseType: ResponseContentType.Blob}).pipe(
    map(res => res.blob()),
    map(blob => {
      let urlCreator = window.URL;
      return  this._sanitizer.bypassSecurityTrustUrl(urlCreator.createObjectURL(blob));
    }),)
   }


   
   getHash(fileToUpload: File, key: string): Observable<any> {
    const url = this.serverUrl + '/eblogimage/upload/'+ key ;
    const formData: FormData = new FormData();
    formData.append('file', fileToUpload, fileToUpload.name);
    return this.http.post(url, formData).pipe(
      map(() => { return true; }),
      catchError(err =>{
        return observableThrowError (err)
      }),);
   }

   getHashURL(URL:any, key: string): Observable<any> {
    const url = this.serverUrl + '/eblogimage/uploadbyurl/'+ key ;
     var headers = new Headers ({
      'Content-Type' : 'application/json' });  
    return this.http.post(url, URL ,{headers: headers}).pipe(
      map(() => { return true; }),
      catchError(err =>{
        return observableThrowError (err)
      }),);
   }

   deleteHash(key:string): Observable<any>{
     const url =  this.serverUrl + '/eblogimage/delete/'+ key;
     return this.http.delete(url).pipe(
     map(() => { return true; }),
     catchError(err =>{
       return observableThrowError (err)
     }),);
   
   }
 
}
