import { Component, OnInit, OnDestroy, DoCheck } from '@angular/core';
import { PostService, LoaderService } from '../../../_services';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Get, User } from '../../../models';

import { OrderByPipe } from '../../../pipes';
import { AuthService } from '../../../_services/auth.service';
import { MatDialogModule } from '@angular/material';
import { ImagesService } from '../../../_services';
import { Posts } from '../../../models';

@Component({
  selector: 'app-get',
  templateUrl: './get.component.html',
  styleUrls: ['./get.component.css'],
  providers: [PostService]
})
export class GetComponent implements OnInit {
  
  blog=[];
  get: Get = new Get();
  data :  any;
  interval: any;
  post= [];
  posts: Posts = new Posts();
  errorMessage = "";
  postId: string = "";
  user : any;
  User : User = new User();
  username: string;
  constructor(
    
            private postService : PostService,
            private router : Router,
            private route : ActivatedRoute,
            private authService : AuthService,
            public dialog: MatDialogModule,
            private imageService: ImagesService,
            private loaderService: LoaderService,
  ) { 
       this.user =  this.authService.getCurrentUser();
      }

  
      private showLoader(): void {
        this.loaderService.show();
        }
        private hideLoader(): void {
        this.loaderService.hide();
        } 
  ngOnInit() {
       this.showLoader();





    window.scrollTo(0,0);
   
      this.User.username = this.user.username;
    return this.postService.getEblog(this.User).subscribe(res =>{
           
      // console.log(res);
      this.posts = res.blog;
      if(res.blog[0] != undefined){
     
      this.get = res.blog;
             this.posts = res.blog;

    
               const length:any = this.get;
             for (var i=0; i < length.length ;i++ ){
                
             
        var date_asc = function(date1, date2) {
          if(date1 > date2) return 1;
          if(date1 < date2) return -1;
          return 0;
        };
        var date_desc = function(a, b){
          if(a.createdtime < b.createdtime) return 1;
          if(a.createdtime > b.createdtime) return -1;
          return 0;
        }

       


      }
          
              var date :any =   this.posts
              var sort  : Get [] ;
             
              sort = date.slice(0) 
              this.hideLoader();
              sort.sort(date_desc);
              this. post  =  sort;
            
                
             
      }
      if(res.blog[0] == undefined){
      this.errorMessage = "oops!";
      }
     
    });
     
  
  }

 


  clickMethod(title: string, id:string, blogUser:string ) {
  
    if(confirm("Are you sure want to delete ?" + title.replace(/-/g, ' ') )) {
     
      this.showLoader();

     this.postService.deleteEblogs(id,blogUser).subscribe(res =>{
      let key = id ;
      this.imageService.deleteHash(key).subscribe(res=>{

      
          
     this.postService.removeAd(key).subscribe(res=>{
    
    
          this.postService.removeComment(key).subscribe(res =>{
            this.hideLoader();   

            window.location.reload();
    });    
        
     });
                 
    });
  });
        
             
          //  window.location.reload();
         


    }else{
       this.hideLoader();
      // this.router.navigate(['/create']);
     
    }
  }

  

}
