export class BottomSheetOverviewFeedSheet  {


  public RL: ReadList = new ReadList();
  user:any;
  url: SMURL = new SMURL();
  get : Get = new Get();
  blog:any=[];
  public  post:Post = new Post();
  public posts:Posts = new Posts();

  constructor( private ridService: ReadlistService ,
    private route : ActivatedRoute,
    private postService : PostService,
    private postMService: PostMService,
    private authService : AuthService,
    private bottomSheetRef: MatBottomSheetRef<BottomSheetOverviewFeedSheet>,
    @Inject (SocialmediaService) public socialMedia: SocialmediaService,
    private loaderService: LoaderService,
  
   ) {

      this.user = this.authService.getCurrentUser();
    
      
    }
  
    private showLoader(): void {
      this.loaderService.show();
      }
      private hideLoader(): void {
      this.loaderService.hide();
      }  
      

submit(event:MouseEvent){
  event.preventDefault();
  this.showLoader();
  this.url.id = this.user.id;

   
  this.socialMedia.addURL(this.url).subscribe(res =>{
    
    this.hideLoader();
    window.location.reload();
  });
  this.bottomSheetRef.dismiss(); 



}
  
}