import { Component, OnInit, AfterViewInit ,ViewChild} from '@angular/core';
import * as MediumEditor from 'medium-editor';
import * as MediumInsert from 'medium-editor-insert-plugin-es6';
import * as MediumButton   from 'medium-button';
import { Post, Posts, Get } from '../../../models';
import { PostMService } from '../../../_services/postm.service'
import { v4 as uuid } from 'uuid';
import { ImagesService, LoaderService } from '../../../_services';

@Component({
  selector: 'app-medium-editor',
  templateUrl: './medium-editor.component.html',
  styleUrls: ['./medium-editor.component.css']
})
export class MediumEditorComponent implements OnInit,AfterViewInit {
  @ViewChild('editor') editor;
  postM : Posts = new Posts();
  title1:string;
  public key: string ;
  keyUp:boolean;
  fileToUpload: File =null;
  constructor( private postMob : PostMService,private imageService:ImagesService, private loaderService: LoaderService) { }

  private showLoader(): void {
    this.loaderService.show();
    }
    private hideLoader(): void {
    this.loaderService.hide();
    } 
  ngOnInit() {
    window.scrollTo(0,0);

  }
  hideTab(){

    this.keyUp = true;
    }
  ngAfterViewInit(){
    this. editor = new MediumEditor ('#contentEditable',{
      theme:'default',
      buttonLabels: 'fontawesome',
       extensions:{
        'pop':  new MediumButton({label:'<b>HR</b>', start:'<br><hr style="border-bottom:1px dashed grey;">', end:'</hr><br>'}),
          'insert': new  MediumInsert({
          // editor: editor,
        
          addons: { 
              embed:{
              //   oembedProxy: '//iframe.ly/api/oembed?api_key=9992ca1f484ad28571bb9f'
  
              },
              images: { // (object) Image addon configuration
                  label: '<span class="fa fa-camera"></span>', // (string) A label for an image addon
                  uploadScript: null, // DEPRECATED: Use fileUploadOptions instead
                  deleteScript: 'delete.php', // (string) A relative path to a delete script
                  deleteMethod: 'POST',
                  fileDeleteOptions: {}, // (object) extra parameters send on the delete ajax request, see http://api.jquery.com/jquery.ajax/
                  preview: true, // (boolean) Show an image before it is uploaded (only in browsers that support this feature)
                  captions: true, // (boolean) Enable captions
                  captionPlaceholder: 'Type caption for image (optional)', // (string) Caption placeholder
                  autoGrid: 3, // (integer) Min number of images that automatically form a grid
                  formData: {}, // DEPRECATED: Use fileUploadOptions instead
                  fileUploadOptions: { // (object) File upload configuration. See https://github.com/blueimp/jQuery-File-Upload/wiki/Options
                      url:'', // (string) A relative path to an upload script
                      acceptFileTypes: /(\.|\/)(gif|jpe?g|png)$/i // (regexp) Regexp of accepted file types
                  },
                  styles: { // (object) Available image styles configuration
                      wide: { // (object) Image style configuration. Key is used as a class name added to an image, when the style is selected (.medium-insert-images-wide)
                          label: '<span class="fa fa-align-justify"></span>', // (string) A label for a style
                          added: function($el) {}, // (function) Callback function called after the style was selected. A parameter $el is a current active paragraph (.medium-insert-active)
                          removed: function($el) {} // (function) Callback function called after a different style was selected and this one was removed. A parameter $el is a current active paragraph (.medium-insert-active)
                      },
                //       left: {
                //           label: '<i class="fas fa-align-left"></i>'
                //       },
                //       right: {
                //           label: '<i class="fas fa-align-right"></i>'
                //       },
                //       grid: {
                //           label: '<i class="fas fa-grip-horizontal"></i>'
                //       }
                //   },
                  actions: { // (object) Actions for an optional second toolbar
                      remove: { // (object) Remove action configuration
                          label: '<span class="fa fa-times"></span>', // (string) Label for an action
                          clicked: function($el) { // (function) Callback function called when an action is selected
                              var $event = $.Event('keydown');
                              $event.which = 8;
                              $(document).trigger($event);
                          }
                      }
                  },
                  messages: {
                      acceptFileTypesError: 'This file is not in a supported format: ',
                      maxFileSizeError: 'This file is too big: '
                  },
                  uploadCompleted: function($el, data) {}, // (function) Callback function called when upload is completed
                  uploadFailed: function(uploadErrors, data) {
                          alert('There is a problem in uploading the image');
                          console.log(uploadErrors);
                          console.log(data);
                      } // (function) Callback function called when upload failed
              }
        
              
                }}
      })
        
       }
    })   ;
  
  }




  postMobile(){
      this.showLoader();
     this.key = uuid();
     var str  = this.title1;
     this.postM.title = str.replace(/\s+/g, '-');
     this.postM._id = this.key ;
     this.postM.content = this.editor.getContent();
      this.postMob.postM(this.postM).subscribe(res =>{
          this.hideLoader();
          window.location.reload();
      })
    
    
    
    }

    imageUpload(files:FileList){
          this.showLoader();
        this.fileToUpload = files.item(0);
        this.key = uuid();
        var str  = this.title1;
        this.postM.title = str.replace(/\s+/g, '-');
        this.postM._id = this.key ;
        this.postM.content = this.editor.getContent();
          this.postMob.postM(this.postM).subscribe(res =>{
           this.imageService.getHash(this.fileToUpload,this.postM._id).subscribe(res =>{
           this.hideLoader();
            window.location.reload();

           })
      })
    }
    
}
