import { Component, OnInit, Inject, forwardRef,AfterViewInit } from '@angular/core';
import { ActivatedRoute , Params, Router} from '@angular/router';
import { PostMService } from '../../_services/postm.service';
import { Posts, Post, Ad, Comments,Comment, User, Users, Ads } from '../../models';
import { PostService, AuthService, LoaderService } from '../../_services';
import { SocialmediaService } from '../../_services/socialmedia.service';
import * as MediumEditor from 'medium-editor';
import * as MediumInsert from 'medium-editor-insert-plugin-es6';
import * as MediumButton   from 'medium-button';

@Component({
  selector: 'app-stories',
  templateUrl: './stories.component.html',
  styleUrls: ['./stories.component.css']
})
export class StoriesComponent implements OnInit,AfterViewInit {
  username: string;
  postId = [];
  postM: Posts = new Posts();
  public title: string;
  public id :string;
  posts: Posts = new Posts();
  session: Posts = new Posts();
  post :Post = new Post();
  authorBlog=[] ;
  authorStory=[] ;
  isPushed:boolean;
  User:User = new User() ;
  user: Users = new Users();
  ad: Ad = new Ad();
  ads: Ads = new Ads();
  code:string;
  comments: Comments = new Comments();
  comment:  Comment = new Comment();
  valueChange:string;
  commentsLength: string;
  fb: string = 'https://www.facebook.com/EBloginc-111213763095386/?ref=bookmarks';
   twitter: string = 'https://twitter.com/Eblog4';
   insta: string ='https://www.instagram.com/ebloginc/';
   linkedIn: string = 'https://www.linkedin.com/in/e-blog-122031166/';
   gPlus: string ='https://plus.google.com/u/1/117728075674733939607';
   youtube:string = 'https://www.youtube.com/channel/UC08A3ACzDhC4K9djxVOj62w';
   github:string = 'https://github.com/ebloginc';
  constructor( private route: ActivatedRoute,
               private postMService: PostMService,
               private router : Router,
               private postService: PostService,
               private authService: AuthService ,
               private loaderService: LoaderService,
               @Inject (SocialmediaService) public socialService: SocialmediaService
              ) { }

private showLoader(): void {
this.loaderService.show();
}
private hideLoader(): void {
this.loaderService.hide();
}           

  ngOnInit() {       

    window.scrollTo(0,0);
    this.showLoader();

    this.route.params.switchMap((params : Params) =>{

      this. username = params['username'];
      this.title = params['title'];
      this.id = params ['id'];
      this.posts.title =this.title;
      this.posts._id = this.id;
      return this.postMService.getPostMById(this.posts);
      
    }).subscribe(res =>{
           
      if(res.blog[0] != null|| undefined){
            
                  // this.hideLoader();
                  this.post= res;




                  this.postService.getAd(this.id).subscribe(res =>{
                    this.ads = res;
                    // console.log(this.ads);
                  });
            
                  this.postService.getComment(this.id).subscribe(res  => {
                    // console.log(res);
                  this.comment = res;
                   var length: any = res.cmt;
                   this.commentsLength = length.length;
                  
                  
             });


    this.postMService.getPostMByExceptId(this.id).subscribe(res =>{
      // console.log(res);
      var array:any = res.blog;
       function shuffle(array){
      for(var i = 0; i <array.length; i++ ){
        var j  = i + Math.floor(Math.random() * (array.length -i));

        var temp = res.blog[j];
        res.blog[j] = res.blog[i];
        res.blog[i] = temp;


      }
      return array;
    }

    this.postId = shuffle(array);  
    this.hideLoader();
    });
                    
      }else{
           this.router.navigate(['/404-not-found']);  
      }
    

  
   

    this.authService.getUsersByUsername(this.username).subscribe(res=>{
      this.user.user = res.data;
      this.socialService.getURL(res.data._id).subscribe(res =>{
        if(res.data !== null){
        this.fb = res.data.fb || 'https://www.facebook.com/EBloginc-111213763095386/?ref=bookmarks';
        this.linkedIn = res.data.linkedin || 'https://www.linkedin.com/in/e-blog-122031166/';
        this.twitter = res.data. twitter || 'https://twitter.com/Eblog4';
        this.insta = res.data.insta || 'https://www.instagram.com/ebloginc/';
        this.gPlus = res.data.gPlus || 'https://plus.google.com/u/1/117728075674733939607' ;
        this.youtube = res.data.youtube || 'https://www.youtube.com/channel/UC08A3ACzDhC4K9djxVOj62w';
        this.github = res.data.github || 'https://github.com/ebloginc';
        }
    });
      });

    });
    
  }
  ngAfterViewInit(){
    
  }
  

  sessionUser():boolean{
     
    if(this.User  === this.username){
     
     
      return true;
    }else{
    
      return false;
    }
    
 }



 adSubmit(){

  this.isPushed = true;
  this.showLoader();
    this.postService.adUrl(this.ad,this.id).subscribe(res =>{
    this.hideLoader();
     window.location.reload();
    });
 }

 update(){
  // this.valueChange.emit(this.code);
  // console.log(this.code);
  // this.ad = this.code;
}
onCommentSubmit(value){
       
         this.isPushed = true;
         this.showLoader();
   this.postService.addComment(this.comments,this.id).subscribe(res =>{
         this.hideLoader();
       window.location.reload();
  
   })
  
}



loggin(): boolean {
  if (this.authService.loggedIn() ) {
   return true;
 } else {
   return false;
 }
 
  }

  profile(value){
    // this.showLoader();

    this.router.navigate(['/eblogger/',value]);
  }

  otherStory(id:string,username:string,title:string){
    this.showLoader();
    this.router.navigate(['/stories/',id,username,title]);
    window.scrollTo(0,0);

    // setTimeout(() => {
    //   this.hideLoader();

    // }, 3000);
  }

}
